<template>
  <section>
    <el-row>
      <el-col :span="24">
        <!--表单-->
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="订单号">
            <el-autocomplete
              class="inline-input width-140" v-model="formInline.orderCode" :fetch-suggestions="querySearchName"
              placeholder="请输入订单号" :trigger-on-focus="true">
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="商品名称">
            <el-autocomplete
              class="inline-input width-140" v-model="formInline.name" :fetch-suggestions="querySearchName"
              placeholder="请输入商品名称" :trigger-on-focus="false">
            </el-autocomplete>
          </el-form-item>
          <el-form-item label="支付时间">
            <el-date-picker v-model="formInline.paytime"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['00:00:00']">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="下单手机号">
            <el-input v-model="formInline.tel" placeholder="请输入下单手机号" classs="width-140"></el-input>
          </el-form-item>
          <el-form-item label="支付方式">
            <el-select v-model="formInline.putaway" placeholder="请选择" classs="width-140">
              <el-option v-for="(item,index) in putaways" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="优惠方式">
            <el-select v-model="formInline.youhui" placeholder="请选择" classs="width-140">
              <el-option v-for="(item,index) in youhuis" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="下单时间">
            <el-date-picker v-model="formInline.downtime"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['00:00:00']">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="订单状态">
            <el-select v-model="formInline.state" placeholder="请选择" classs="width-140">
              <el-option v-for="(item,index) in putaways" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="商品来源">
            <el-select v-model="formInline.source" placeholder="请选择" classs="width-140">
              <el-option v-for="(item,index) in sources" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="是否0元支付">
            <el-select v-model="formInline.zeropay" placeholder="请选择" classs="width-140">
              <el-option v-for="(item,index) in zeropays" :label="item.label" :value="item.value" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
            <el-button  id="download" class="downexcel" @click="download()" download="download.csv">导出</el-button>
          </el-form-item>
        </el-form>
        <!--表格-->
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column  prop="userid" label="下单人id"></el-table-column>
          <el-table-column prop="usertel" label="下单人手机号"></el-table-column>
          <el-table-column  prop="orderdate" label="下单时间"></el-table-column>
          <el-table-column  prop="ordercode" label="订单号"></el-table-column>
          <el-table-column  prop="laiyuan" label="商品来源"></el-table-column>
          <el-table-column  prop="namspu" label="商品名称/spu"></el-table-column>
          <el-table-column  prop="detail" label="商品详情"></el-table-column>
          <el-table-column  prop="paymoney" label="实付金额"></el-table-column>
          <el-table-column  prop="orderdate" label="支付时间"></el-table-column>
          <el-table-column  prop="paytype" label="支付方式"></el-table-column>
          <el-table-column  prop="orderstate" label="订单状态"></el-table-column>
          <el-table-column  prop="shouhuo" label="收货信息"></el-table-column>
          <el-table-column  prop="wuliu" label="物流状态"></el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <div class="table-buttonbox">
                <el-button size="small" @click="fnorderDetail(scope.$index, scope.row)">详情</el-button>
              </div>
              <div class="table-buttonbox">
                <el-button size="small" @click="fnexpressage(scope.$index, scope.row)">物流</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="block">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage"
            :page-size="pageSize" layout="prev, pager, next, jumper" :total="totalNum">
          </el-pagination>
        </div>
      </el-col>
    </el-row>
    <el-dialog title="物流信息" :visible.sync="diaExpressage" size="tiny" center class="dia-detailedit"  @close="diaExpressage = false">
      <el-form ref="form" :model="expressage" label-width="120px">
        <el-form-item label="物流公司">
          <el-input v-model="expressage.name" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="物流单号">
          <el-input v-model="expressage.ordercode" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="物流扭转信息">
          <div v-html="expressage.desc" class="expressage-text"></div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </section>
</template>
<script>
  const ERR_OK = "000";
  export default {
    data () {
      return {
        formInline: {
          orderCode:'',
          name: '',
          tel: '',
          putaway:'1',
          youhui:'1',
          downtime:'',
          state:'1',
          source:'1',
          zeropay:'1',
          category:[],
          paytime:'',
        },
        putaways:[{label:'全部',value:'1'},{label:'支付宝',value:'2'},{label:'微信',value:'3'},{label:'一网通',value:'4'},{label:'银联',value:'5'},{label:'银联',value:'5'}],
        youhuis:[{label:'全部',value:'1'},{label:'妙币',value:'2'},{label:'激励金',value:'3'}],
        state:[{label:'全部',value:'1'},{label:'未付款',value:'2'},{label:'待发货',value:'3'},{label:'待收货',value:'4'},{label:'已完成',value:'5'},{label:'已取消',value:'6'}],
        sources:[{label:'全部',value:'1'},{label:'网易严选',value:'2'},{label:'京东',value:'3'}],
        zeropays:[{label:'全部',value:'1'},{label:'是',value:'2'},{label:'否',value:'3'}],
        tableData: [
          {
            userid:'123456',usertel:'15701360592',orderdate:'2018-18-18 12:12:00',ordercode:'123456',laiyuan:'京东',namspu:'裤子/123456',detail:'ss',paymoney:'100',paytype:'微信',orderstate:'已付款',shouhuo:'qisibo\n15701360222\n北京',wuliu:'已发货'
          }
        ],
        options: [],
        currentPage: 1,
        pageSize: 10,
        totalNum: 86,
        table_index: 999,
        diaExpressage:false,
        expressage:{
          name:'',
          ordercode:'',
          desc:'asdf',
        },
        restaurantsName:[
          { "value": "三全鲜食（北新泾店）", "address": "长宁区新渔路144号" },
          { "value": "Hot honey 首尔炸鸡（仙霞路）", "address": "上海市长宁区淞虹路661号" },
          { "value": "新旺角茶餐厅", "address": "上海市普陀区真北路988号创邑金沙谷6号楼113" },
          { "value": "泷千家(天山西路店)", "address": "天山西路438号" },
          { "value": "胖仙女纸杯蛋糕（上海凌空店）", "address": "上海市长宁区金钟路968号1幢18号楼一层商铺18-101" },
          { "value": "贡茶", "address": "上海市长宁区金钟路633号" },
        ],//模拟数据
      };
    },
    created () {
       this.formInline.paytime=this.ntradeDate(-30*24*60*60*1000);
       this.formInline.downtime=this.ntradeDate(-30*24*60*60*1000);
    },
    methods: {
      onSubmit () {console.log(this.formInline.paytime);
        this.$message('模拟数据，这个方法并不管用哦~');
      },
      fnorderDetail () {
        this.$router.push({path:'/order/detail'});
      },
      fnexpressage (){
        this.diaExpressage=true;
      },
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        console.log(`当前页: ${val}`);
      },
      download: function() {
        // console.log("xiazai")
        // var obj = document.getElementById('download');
        // var str = "姓名,出生日期,地址\n";
        // for (var i = 0; i < this.tableData.length; i++) {
        //   var item = this.tableData[i];
        //   str += item.name + ',' + item.date + ',' + item.address;
        //   str += "\n";
        // }
        // console.log(obj)
        // str = encodeURIComponent(str);
        // console.log(str)
        // obj.href = "data:text/csv;charset=utf-8,\ufeff" + str;
        // obj.download = "download.csv";
      },
      querySearchName(queryString, cb) {
        var restaurantsName = this.restaurantsName;
        var results = queryString ? restaurantsName.filter(this.createFilter(queryString)) : restaurantsName;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      ntradeDate(time){
        //明天的时间   --终止时间
        var ntoday = new Date();
        ntoday.setTime(ntoday.getTime()+time);
        return [ntoday,new Date()] ; 
      },
    }
  };
</script>
<style>
  .width-140{
    width:140px;
  }
  .el-pagination {
    text-align: center;
    margin-top: 30px;
  }
  .expressage-text{
    color:#999;
  }
</style>



